import Vue from 'vue'
import Router from 'vue-router'


import Home from '@/vieuws/Home'
import Signify from '@/vieuws/Signify'
import Broadcast from '@/vieuws/Broadcast'
import Livestream from '@/vieuws/Livestream'
import Robeco from '@/vieuws/Robeco'
import Carcontrol from '@/vieuws/Carcontrol'
import Peterplantenbak from '@/vieuws/Peterplantenbak'
import Caroverview from '@/vieuws/Caroverview'



Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            name: '/Home',
            component: Home
        },
        {
            path:'/signify',
            name: '/Sifnify',
            component: Signify
        },
        {
            path:'/broadcast/:id',
            name: '/Broadcast',
            component: Broadcast,
            props: true,
        },
        {
           path:'/robeco',
           name: '/Robeco',
           component: Robeco
        },
        {
            path:'/livestream',
            name: '/Livestream',
            component: Livestream
        },
        {
            path:'/carcontrol',
            name: '/Carcontrol',
            component: Carcontrol
        },
        {
            path:'/peterplantenbak',
            name: '/Peterplantenbak',
            component: Peterplantenbak
        },
        {
            path:'/caroverview',
            name: '/Caroverview',
            component: Caroverview
        },
    
        
        

    ]
})